import axios from "axios";

import store from "../store";
import { integratedRemoteFetch } from "../assets/data/DataUtil.js";

/**
 * axios設定檔
 */
export const config = {
  // `url` 是用于请求的服务器 URL
  url: "/user",

  // `method` 是创建请求时使用的方法
  method: "get", // 默认值

  // `baseURL` 将自动加在 `url` 前面，除非 `url` 是一个绝对 URL。
  // 它可以通过设置一个 `baseURL` 便于为 axios 实例的方法传递相对 URL
  baseURL: "https://opendata.cwb.gov.tw/api/v1/rest/datastore/",

  // `transformRequest` 允许在向服务器发送前，修改请求数据
  // 它只能用于 'PUT', 'POST' 和 'PATCH' 这几个请求方法
  // 数组中最后一个函数必须返回一个字符串， 一个Buffer实例，ArrayBuffer，FormData，或 Stream
  // 你可以修改请求头。
  transformRequest: [
    function (data, headers) {
      // 对发送的 data 进行任意转换处理

      return data;
    },
  ],

  // `transformResponse` 在传递给 then/catch 前，允许修改响应数据
  transformResponse: [
    function (data) {
      // 对接收的 data 进行任意转换处理

      return data;
    },
  ],

  // `timeout` 指定请求超时的毫秒数。
  // 如果请求时间超过 `timeout` 的值，则请求会被中断
  timeout: 1000, // 默认值是 `0` (永不超时)

  // `withCredentials` 表示跨域请求时是否需要使用凭证
  withCredentials: false, // default

  // 自定义请求头
  // headers: { "X-Requested-With": "XMLHttpRequest" },

  // `responseType` 表示浏览器将要响应的数据类型
  // 选项包括: 'arraybuffer', 'document', 'json', 'text', 'stream'
  // 浏览器专属：'blob'
  responseType: "json", // 默认值

  // `onUploadProgress` 允许为上传处理进度事件
  // 浏览器专属
  onUploadProgress: function (progressEvent) {
    // 处理原生进度事件
  },

  // `onDownloadProgress` 允许为下载处理进度事件
  // 浏览器专属
  onDownloadProgress: function (progressEvent) {
    // 处理原生进度事件
  },
};

/**
 * 氣象平台Axios實體
 */
const instance = axios.create(config);
// 添加请求拦截器
instance.interceptors.request.use(
  function (config) {
    // 在发送请求之前做些什么
    return config;
  },
  function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  }
);

// 添加响应拦截器
instance.interceptors.response.use(
  function (response) {
    // 2xx 范围内的状态码都会触发该函数。
    // 对响应数据做点什么
    integratedRemoteFetch(response, null);
    return response;
  },
  function (error) {
    // 超出 2xx 范围的状态码都会触发该函数。
    // 对响应错误做点什么
    store.dispatch("apiSotre/commitApiMsg", error.response);
    integratedRemoteFetch(null, error);

    return Promise.reject(error);
  }
);

/**
 * 呼叫氣象平台Axios實體取得天氣資訊
 * @param {string} cityName 城市
 * @param {string} townname 鄉鎮區
 * @returns {Promise} 天氣結果
 */
export function getWeather(cityName, townname) {
  return instance
    .get("F-D0047-093", {
      params: {
        Authorization: "CWB-BB5E0510-6698-4BF9-87FF-52ED85F6418D",
        locationId: cityName,
        locationName: townname,
      },
    })
    .then((res) => res)
    .catch((error) => error);
}
