/**
 * 取得某日台股資訊
 * @returns {Promise} 功能資訊
 */
export const getStorcktwInfo = () => {
  let getInfo = null;
  let msgFmt = null;
  if (true) {
    msgFmt = new MsgFormat(
      COR_E0001,
      MSG_TYPE_SUCCESS,
      MSG_POP_ASIDE
    ).getValue();
    getInfo = {
      data: JSON.parse(JSON.stringify(tw)),
      msg: { ...msgFmt },
    };
  } else {
    msgFmt = new MsgFormat(
      ERR_E0003,
      MSG_TYPE_SUCCESS,
      MSG_POP_ASIDE
    ).getValue();
  }

  return integratedFetch(getInfo, msgFmt, 300);
};
